import React, { useState } from 'react';
import './Login.css';

// eslint-disable-next-line no-undef
const Login = () => {
  const [isSigningIn, setIsSigningIn] = useState(false);
  const [error, setError] = useState(null);

  const handleYahooSignIn = async () => {
    setIsSigningIn(true);
    setError(null);

    try {
      // eslint-disable-next-line no-undef
      const auth = firebase.auth();
      // eslint-disable-next-line no-undef
      const provider = new firebase.auth.YahooAuthProvider();
      await auth.signInWithPopup(provider);
      // Auth state change will be detected by App.js and redirect
    } catch (err) {
      console.error('Yahoo Sign-In Error:', err);
      setError(err.message);
      setIsSigningIn(false);
    }
  };

  const handleSignOut = () => {
    // eslint-disable-next-line no-undef
    firebase.auth().signOut().catch(err => {
      console.error('Sign out error:', err);
    });
  };

  return (
    <div className="login-container">
      <div className="login-content">
        <div className="login-left">
          <img src="/Buddy.png" alt="Buddy" className="buddy-image-large" />
        </div>
        
        <div className="login-right">
          <div className="login-box">
          <h2>Sign in to Buddy</h2>
          
          {error && (
            <div className="error-message">
              {error}
            </div>
          )}

          <button 
              className="yahoo-button"
              onClick={handleYahooSignIn}
              disabled={isSigningIn}
            >
              {isSigningIn ? (
                <>
                  <span className="spinner"></span>
                  Signing in...
                </>
              ) : (
                <>
                  <span className="yahoo-icon"></span>
                  Sign in with Yahoo
                </>
              )}
            </button>
            
            <p className="login-note">
              Secure login powered by Yahoo
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Login;
